"use client";

import { useEffect, useState } from "react";
import { MoreVertical, X } from "lucide-react";
import { END_POINT } from "@/config";

export default function ProductsPage() {

  const [products, setProducts] = useState([]);
  const [categories, setCategories] = useState([]);
  const [showModal, setShowModal] = useState(false);
  const [loading, setLoading] = useState(false);

  // product form
  const [colors, setColors] = useState([]);
  const [sizes, setSizes] = useState([]);
  const [currColor, setCurrColor] = useState('');
  const [currSize, setCurrSize] = useState('');
  const [formData, setFormData] = useState({
    title: "",
    price: "",
    desc: "",
    category: "",
    images: [],
  });

  // ðŸ”¥ ACTION MENU STATE
  const [menuPosition, setMenuPosition] = useState(null);
  // { productId, x, y }

  /* =======================
      HELPERS
  ======================= */
  const clearForm = () => {
    setFormData({
      title: "",
      price: "",
      desc: "",
      category: "",
      images: [],
    });
    setColors([]);
    setSizes([]);
  };

  const closeModal = () => {
    clearForm();
    setShowModal(false);
  };

  /* =======================
      COLORS / SIZES
  ======================= */
  const addColor = () => {
    if (!currColor.trim()) return;
    setColors(prev => [...prev, ...currColor.trim().split(' ')]);
    setCurrColor('');
  };

  const addSize = () => {
    if (!currSize.trim()) return;
    setSizes(prev => [...prev, ...currSize.trim().split(' ')]);
    setCurrSize('');
  };

  const removeColor = (color) =>
    setColors(prev => prev.filter(c => c !== color));

  const removeSize = (size) =>
    setSizes(prev => prev.filter(s => s !== size));

  /* =======================
      FETCH DATA
  ======================= */
  const getAllProducts = async () => {
    try {
      const res = await fetch(END_POINT.PRODUCTS, {
        method: "GET",
        headers: { "Content-Type": "application/json" },
        credentials: "include",
      });
      const json = await res.json();
      setProducts(json.data || []);
    } catch (error) {
      console.error(error.message);
    }
  };

  const getAllCategories = async () => {
    const res = await fetch(END_POINT.CATEGORIES(''), {
      method: 'GET',
      headers: { "Content-Type": "application/json" },
      credentials: 'include',
    });
    const json = await res.json();
    setCategories(json.data || []);
  };

  /* =======================
      FORM HANDLERS
  ======================= */
  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
  };

  const handleFileChange = (e) => {
    setFormData(prev => ({ ...prev, images: e.target.files }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);

    const body = new FormData();
    body.append("title", formData.title);
    body.append("price", formData.price);
    body.append("desc", formData.desc);
    body.append("category", formData.category);
    body.append("colors", JSON.stringify(colors));
    body.append("sizes", JSON.stringify(sizes));

    for (let img of formData.images) {
      body.append("images", img);
    }

    try {
      const res = await fetch(END_POINT.PRODUCTS, {
        method: "POST",
        body,
        credentials: "include",
      });

      const json = await res.json();
      if (json.successful) {
        clearForm();
        setShowModal(false);
        getAllProducts();
      }
    } catch (err) {
      console.error(err.message);
    } finally {
      setLoading(false);
    }
  };

  /* =======================
      PRODUCT ACTIONS
  ======================= */
  const handleAction = (action, productId) => {
    switch (action) {
      case 'edit':
        console.log('edit', productId);
        break;
      case 'delete':
        console.log('delete', productId);
        break;
      default:
        break;
    }
    setMenuPosition(null);
  };

  /* =======================
      CLOSE MENU ON SCROLL
  ======================= */
  useEffect(() => {
    const close = () => setMenuPosition(null);
    window.addEventListener('scroll', close);
    window.addEventListener('resize', close);
    return () => {
      window.removeEventListener('scroll', close);
      window.removeEventListener('resize', close);
    };
  }, []);

  /* =======================
      INITIAL LOAD
  ======================= */
  useEffect(() => {
    getAllProducts();
    getAllCategories();
  }, []);

  return (
    <div className="p-8 flex-1">

      {/* HEADER */}
      <div className="flex items-center justify-between mb-8">
        <div>
          <h1 className="text-3xl font-bold">Products</h1>
          <p className="text-gray-500 mt-1">
            Manage all products in Threadix
          </p>
        </div>
        <button
          onClick={() => setShowModal(true)}
          className="px-5 py-2.5 bg-black text-white rounded-xl hover:bg-gray-900"
        >
          + Add Product
        </button>
      </div>

      {/* TABLE */}
      <div className="bg-white border rounded-xl shadow-sm overflow-x-auto">
        <table className="w-full min-w-[700px]">
          <thead className="bg-gray-50 border-b">
            <tr>
              <th className="px-6 py-3 text-left">Product</th>
              <th className="px-6 py-3 text-left">Category</th>
              <th className="px-6 py-3 text-left">Price</th>
              <th className="px-6 py-3 text-left">Images</th>
              <th className="px-6 py-3 text-right">Actions</th>
            </tr>
          </thead>

          <tbody>
            {products.map(product => (
              <tr
                key={product._id}
                className="border-b last:border-none hover:bg-gray-50"
              >
                <td className="px-6 py-4 font-medium">
                  {product.title}
                </td>
                <td className="px-6 py-4 text-gray-600">
                  {product.category || "â€”"}
                </td>
                <td className="px-6 py-4 text-gray-600">
                  {product.price} EGP
                </td>
                <td className="px-6 py-4 text-gray-600">
                  {product.images?.length || 0}
                </td>
                <td className="px-6 py-4 text-right">
                  <button
                    onClick={(e) => {
                      const rect = e.currentTarget.getBoundingClientRect();
                      setMenuPosition({
                        productId: product._id,
                        x: rect.right - 160,
                        y: rect.bottom + 8,
                      });
                    }}
                    className="p-2 rounded-full hover:bg-gray-200"
                  >
                    <MoreVertical size={18} />
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      {/* ACTION MENU (FIXED) */}
      {menuPosition && (
        <div
          style={{
            position: 'fixed',
            top: menuPosition.y,
            left: menuPosition.x,
            zIndex: 9999,
          }}
          className="bg-white border rounded-lg shadow-lg w-44"
        >
          <button
            onClick={() => handleAction('edit', menuPosition.productId)}
            className="block w-full text-left px-4 py-2 hover:bg-gray-100"
          >
            Edit
          </button>
          <button
            onClick={() => handleAction('delete', menuPosition.productId)}
            className="block w-full text-left px-4 py-2 hover:bg-gray-100 text-red-600"
          >
            Delete
          </button>
        </div>
      )}

      {/* MODAL */}
      {showModal && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/70 backdrop-blur-sm">

        </div>
      )}

    </div>
  );
}
